import React from 'react';
import { BrowserRouter as Router, Switch, Route} from "react-router-dom";
import { ThrendProvider } from "./Context/Context";
import Thrend from "./Components/Thrend";
import "./mystyle.css";
import Addnewthrend from './Components/Addnewthrend';


function App() {
 
  return (
    <ThrendProvider>
      <Router> 
        <Switch>
          <div className="App">
            <Route exact path="/" component={Thrend} />
            <Route path="/add" component={Addnewthrend} />
          </div>
        </Switch>
      </Router>
    </ThrendProvider>
  );
 
}

export default App;
